<template>
    <div class="pos">
        <div>
            <el-row>
                <el-col :span="7" class="pos-order" id="order-list">
    
                    <el-tabs>
                        <el-tab-pane label="">
                            <el-table :data="tableData" border style="width: 100%">
    
                                <el-table-column prop="goodsName" label=""></el-table-column>
                                <el-table-column prop="count" label="" width="50"></el-table-column>
                                <el-table-column prop="price" label="" width="70"></el-table-column>
                                <el-table-column label="" width="100" fixed="right">
                                    <template scope="scope">
                                        <el-button type="text" size="small" @click="delSingleGoods(scope.row)"></el-button>
                                        <el-button type="text" size="small" @click="addOrderList(scope.row)"></el-button>
    
                                    </template>
                                </el-table-column>
                            </el-table>
    
                            <div class="totalDiv">
                                <small></small>
                                <strong>{{totalCount}}</strong> &nbsp;&nbsp;&nbsp;&nbsp;
                                <small></small>
                                <strong>{{totalMoney}}</strong> 
                            </div>
    
                            <div class="order-btn">
    
                                <el-button type="warning"></el-button>
                                <el-button type="danger" @click="delAllGoods()"></el-button>
                                <el-button type="success" @click="checkout()"> </el-button>
    
                            </div>
                        </el-tab-pane>
    
                        <el-tab-pane label="">
                            
                        </el-tab-pane>
                        <el-tab-pane label="">
                            
                        </el-tab-pane>
    
                    </el-tabs>
    
                </el-col>
    
                <!---->
                <el-col :span="17">
                    <div class="often-goods">
                        <div class="title"></div>
                        <div class="often-goods-list">
    
                            <ul>
                                <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
                                    <span>{{goods.goodsName}}</span>
                                    <span class="o-price">{{goods.price}}</span>
                                </li>
    
                            </ul>
                        </div>
                    </div>
    
                    <div class="goods-type">
    
                        <el-tabs>
                            <el-tab-pane label="">
                                <ul class='cookList'>
    
                                    <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                                        <span class="foodImg">
                                            <img :src="goods.goodsImg" width="100%">
                                        </span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">{{goods.price}}</span>
                                    </li>
    
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="">
                                <ul class='cookList'>
                                    <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                                        <span class="foodImg">
                                            <img :src="goods.goodsImg" width="100%">
                                        </span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">{{goods.price}}</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="">
                                <ul class='cookList'>
                                    <li v-for="goods in type2Goods" @click="addOrderList(goods)">
                                        <span class="foodImg">
                                            <img :src="goods.goodsImg" width="100%">
                                        </span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">{{goods.price}}</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="">
                                <ul class='cookList'>
                                    <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                                        <span class="foodImg">
                                            <img :src="goods.goodsImg" width="100%">
                                        </span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">{{goods.price}}</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
    
                        </el-tabs>
                    </div>
    
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'Pos',
    mounted: function () {
        var orderHeight = document.body.clientHeight;
        document.getElementById("order-list").style.height = orderHeight + 'px';
    },
    created() {
        //
        // axios.get('http://jspang.com/DemoApi/oftenGoods.php')
        //     .then(response => {
        //         //console.log(response);
        //         this.oftenGoods = response.data;
        //     })
        //     .catch(error => {
        //         console.log(error);
        //         alert('');
        //     })
        
        this.oftenGoods = [ { "goodsId":1, "goodsName":"", "price":18 }, { "goodsId":2, "goodsName":"", "price":15 }, { "goodsId":3, "goodsName":"", "price":15 }, { "goodsId":4, "goodsName":"", "price":18 }, { "goodsId":5, "goodsName":"", "price":20 }, { "goodsId":6, "goodsName":"", "price":20 }, { "goodsId":7, "goodsName":"", "price":10 }, { "goodsId":8, "goodsName":"", "price":18 }, { "goodsId":9, "goodsName":"", "price":25 }, { "goodsId":10, "goodsName":"", "price":99 } ];

        //
        /* axios.get('http://jspang.com/DemoApi/typeGoods.php')
            .then(response => {
                //console.log(response);
                //this.oftenGoods=response.data;
                this.type0Goods = response.data[0];
                this.type1Goods = response.data[1];
                this.type2Goods = response.data[2];
                this.type3Goods = response.data[3];

            })
            .catch(error => {
                console.log(error);
                alert('');
            }) */
    },
    data() {
        return {
            tableData: [], //
            oftenGoods: [],
            type0Goods: [
                        {
                        goodsId: 1,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg",
                        goodsName: "",
                        price: 18
                        },
                        {
                        goodsId: 2,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
                        goodsName: "",
                        price: 15
                        },
                        {
                        goodsId: 3,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
                        goodsName: "",
                        price: 15
                        }
            ],
            type1Goods: [
                        {
                        goodsId: 4,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
                        goodsName: "",
                        price: 18
                        },
                        {
                        goodsId: 5,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
                        goodsName: "",
                        price: 20
                        },
                        {
                        goodsId: 6,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg",
                        goodsName: "",
                        price: 20
                        }
            ],
            type2Goods: [
                        {
                        goodsId: 7,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg",
                        goodsName: "",
                        price: 10
                        },
                        {
                        goodsId: 8,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
                        goodsName: "",
                        price: 18
                        }
            ],
            type3Goods: [
                        {
                        goodsId: 9,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg",
                        goodsName: "",
                        price: 25
                        },
                        {
                        goodsId: 10,
                        goodsImg: "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
                        goodsName: "",
                        price: 99
                        }
            ],
            totalMoney: 0, //
            totalCount: 0,  //

            response: [ [ { "goodsId": 1, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg", "goodsName": "", "price": 18 }, { "goodsId": 2, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg", "goodsName": "", "price": 15 }, { "goodsId": 3, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg", "goodsName": "", "price": 15 } ], [ { "goodsId": 4, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg", "goodsName": "", "price": 18 }, { "goodsId": 5, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg", "goodsName": "", "price": 20 }, { "goodsId": 6, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg", "goodsName": "", "price": 20 } ], [ { "goodsId": 7, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg", "goodsName": "", "price": 10 }, { "goodsId": 8, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg", "goodsName": "", "price": 18 } ], [ { "goodsId": 9, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg", "goodsName": "", "price": 25 }, { "goodsId": 10, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg", "goodsName": "", "price": 99 } ] ]
        }
    },
    methods: {
        //
        addOrderList(goods) {
            //console.log(goods);
            this.totalCount = 0; //0
            this.totalMoney = 0;
            let isHave = false;
            //
            for (let i = 0; i < this.tableData.length; i++) {
                console.log(this.tableData[i].goodsId);
                if (this.tableData[i].goodsId == goods.goodsId) {

                    isHave = true; //

                }
            }
            //isHave
            if (isHave) {
                //
                let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
                arr[0].count++;
                //console.log(arr);
            } else {
                //
                let newGoods = { goodsId: goods.goodsId, goodsName: goods.goodsName, price: goods.price, count: 1 };
                this.tableData.push(newGoods);

            }

            this.getAllMoney();
        },
        //
        delSingleGoods(goods) {
            console.log(goods);
            this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
            this.getAllMoney();

        },
        //
        delAllGoods() {
            this.tableData = [];
            this.totalCount = 0;
            this.totalMoney = 0;
        },
        //
        getAllMoney() {
            this.totalCount = 0;
            this.totalMoney = 0;
            if (this.tableData) {
                this.tableData.forEach((element) => {
                    this.totalCount += element.count;
                    this.totalMoney = this.totalMoney + (element.price * element.count);
                });
            }
        },
        //
        checkout() {
            if (this.totalCount!=0) {
                this.tableData = [];
                this.totalCount = 0;
                this.totalMoney = 0;
                this.$message({
                    message: '!',
                    type: 'success'
                });

            }else{
                this.$message.error('');

            }

        }


    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pos {
    font-size: 12px;
}

.pos-order {

    background-color: #F9FAFC;
    border-right: 1px solid #C0CCDA;
}

.order-btn {
    margin-top: 10px;
    text-align: center;
}

.title {
    height: 20px;
    border-bottom: 1px solid #D3DCE6;
    background-color: #F9FAFC;
    padding: 10px;
}

.often-goods-list ul li {
    list-style: none;
    float: left;
    border: 1px solid #E5E9F2;
    padding: 10px;
    margin: 5px;
    background-color: #fff;
    cursor: pointer;
}

.goods-type {
    clear: both;
}

.o-price {
    color: #58B7FF;
}

.often-goods-list {
    border-bottom: 1px solid #C0CCDA;
    height: auto;
    overflow: hidden;
    padding-bottom: 10px;
    background-color: #F9FAFC;
}

.cookList li {
    list-style: none;
    width: 23%;
    border: 1px solid #E5E9F2;
    height: auot;
    overflow: hidden;
    background-color: #fff;
    padding: 2px;
    float: left;
    margin: 2px;
    cursor: pointer;
}

.cookList li span {

    display: block;
    float: left;
}

.foodImg {
    width: 40%;
}

.foodName {
    font-size: 18px;
    padding-left: 10px;
    color: brown;
}

.foodPrice {
    font-size: 16px;
    padding-left: 10px;
    padding-top: 10px;
}

.totalDiv {
    height: auot;
    overflow: hidden;
    text-align: right;
    font-size: 16px;
    background-color: #fff;
    border-bottom: 1px solid #E5E9F2;
    padding: 10px;
}
</style>



